import React from 'react'
import Select from 'react-select';

const SecimKutusu = (props) => {
  return (
    <>
        <Select
            {...props}
            noOptionsMessage={() => 'Herhangi bir veri bulunamad!'}
            placeholder={"Seiniz"}
        />
    </>
  )
}

export default SecimKutusu